/************************** Tablet and Desktop **************************/

@media (min-width: 200px) and (max-width: 279px) {
  .background {
    font-size: 8px;
  }
}
@media (min-width: 280px) and (max-width: 349px) {
  .background {
    font-size: 12px;
  }
}

@media (min-width: 350px) and (max-width: 400px) {
  .background {
    font-size: 16px;
  }
}

@media (min-width: 500px) {
  .background {
    font-size: 22px;
    .time-points {
      .time-point {
        .description {
          font-size: 0.8em;
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .my-apps {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
    grid-gap: 10px;
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
}

@media (min-width: 1024px) {
  .background {
    .time-points {
      display: flex;
      justify-content: space-evenly;
      direction: rtl;
      max-width: 1540px;
      margin: 0 auto;
      .time-point {
        .description {
          width: 25vw;
        }
      }
    }
  }
}

@media (min-width: 1024px) and (max-width: 1539px) {
  .background {
    .time-points {
      .time-point {
        .description {
          max-width: 400px;
          font-size: 0.8em;
        }
      }
    }
  }
}

@media (min-width: 1540px) {
  .background {
    .time-points {
      .time-point {
        .description {
          max-width: 550px;
          font-size: 1.1em;
        }
      }
    }
  }
}

@media (min-width: 1024px) and (max-width: 1699px) {
  .my-apps {
    max-width: 1344px;
  }
}

@media (min-width: 1700px) and (max-width: 1919px) {
  .my-apps {
    max-width: 1700px;
  }
}
@media (min-width: 1920px) and (max-width: 2399px) {
  .my-apps {
    max-width: 1820px;
  }
}
@media (min-width: 2400px) {
  .my-apps {
    max-width: 1820px;
  }
  .background {
    .time-points {
      .time-point {
        .description {
          max-width: 650px;
          font-size: 1.4em;
        }
      }
    }
  }
}
